.profile{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}

.profile img{
    margin-top: 150px;
    padding: 2px;
    background: linear-gradient(267deg, #DA7C25 0.36%,#B923E1 102.06%);
}

.profile h1{
    text-align: center;
    width: 70%;
    font-size: 90px
}

.profile h1 span{
    background: linear-gradient(267deg, #DA7C25 0.36%,#B923E1 102.06%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}

.pro-actions{
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 50px;
}

.act-connect{
    padding: 20px 40px;
    border-radius: 50px;
    background: linear-gradient(267deg, #DA7C25 0.36%,#B923E1 102.06%);
    font-size: 25px;
    font-weight: bold;
    cursor: pointer;
    transition: 0.5s;
}

.act-connect a{
    text-decoration: none;
    color: white;
    transition: 0.5s;
}

.act-connect:hover {
    background: linear-gradient(267deg, #DA7C25 0.36%,#B923E1 70.06%);
    transform: scale(1.05);
}

.act-connect:hover a{
    color: black;
}



@media (max-width: 768px) {
    .profile h1{
        font-size: 50px;
    }
    .profile img{
        width: 200px;
        height: 200px;
    }
    .pro-actions{
        flex-direction: column;
        gap: 0.5rem;
    }
    .act-connect{
        width: 100%;
        text-align: center;
    }
}